.iframe {
  width: 960px;
  height: 630px;
  border: 0;
  padding: 0;
  overflow: hidden;
  background-color: #fff;
  transform-origin: top left;
}
.iframe--small {
  transform: scale(0.104);
}
.iframe--small-mobile {
  width: 460px;
  height: 640px;
  transform: scale(0.104);
}
.iframe--small-legacy {
  width: 846px;
  height: 597px;
  transform: scale(0.092);
}
.iframe--medium {
  transform: scale(0.353);
}
.iframe--medium-mobile {
  width: 460px;
  height: 640px;
  transform: scale(0.353);
}
.iframe--medium-legacy {
  width: 846px;
  height: 597px;
  transform: scale(0.4);
}
.iframe--large {
  transform: none;
}
.iframe--large-mobile {
  width: 460px;
  height: 640px;
  transform: none;
}
.iframe--large-legacy {
  width: 846px;
  height: 597px;
  transform: none;
}
